import React from 'react';
import "./manage-data.css"
import EndzoneNav from '../../../components/navigators/endzone-nav/EndzoneNav'
import { DataGrid } from '@mui/x-data-grid';
import EndzoneButton from '../../../components/buttons/endzone-new-button/endzone-button';
import AddIcon from '@mui/icons-material/Add';
import EndzoneSearchBar from '../../../components/search-bars/endzone-search-bar/endzone-search-bar';
import Footer from '../../../components/misc-items/footer/footer';
import { Button } from '@mui/material';
import axios from 'axios'
import { Alert } from '@mui/material';
import { IconButton } from '@mui/material';
import CloseIcon from '@mui/icons-material/Close';
//remove font-family to fontFamily

class ManageData extends React.Component {
    constructor() {
        super();
        this.state = {
            rows : [],
            alert: {
                style: {display: "none"},
                severity: "info",
                userMessage: ""
            }   
        };
      
        this.columns = [
            { field: 'Home_Team', headerName: 'Home Team', flex : 1},
            { field: 'Away_Team', headerName: 'Away Team', flex : 1},
            { field: 'Game_Year', headerName: 'Game Year', flex : 1, valueGetter : (params) => new Date(params.row.Game_Date).getUTCFullYear()},
            { field: 'Game_Date', headerName: 'Game Date', flex : 1},
            { field : 'Game_Type', headerName : 'Game Type', flex : 1},
            { field : 'Creation_Date', headerName : 'Creation Date', flex : 1},
            { field : 'View Game', flex : 1, renderCell:(cellValues)=>{
                return(
                    <Button variant = "contained" color = "primary" onClick = {(e) => {
                        // console.log(e) 
                        let gameId = cellValues.row.Game_ID
                        window.location.href=`/endzone/data/view?gameId=${gameId}`
                    }}>View</Button>
                )
            }},
            { field : 'Load Game', flex : 1, renderCell:(cellValues)=>{
                return(
                    <Button variant = "contained" color="primary" onClick = {(e)=>{
                        let gameId = cellValues.row.Game_ID
                        let gameType = cellValues.row.Game_Type
                        let homeTeam = cellValues.row.homeTeam
                        let awayTeam = cellValues.row.awayTeam
                        if (gameType == "manual"){
                            window.location.href=`/endzone/data/game/manual?gameId=${gameId}&homeTeam=${homeTeam}&awayTeam=${awayTeam}`
                        }
                        else if (gameType == "ai"){
                            window.location.href=`/endzone/data/game/auto?gameId=${gameId}&homeTeam=${homeTeam}&awayTeam=${awayTeam}`
                        }
                    }}>Load</Button>
                )
            }},
            { field : 'Delete Game', flex : 1, renderCell:(cellValues)=>{
                return(
                    <Button variant = "contained" color ="primary" onClick = {(e)=>{
                        let gameId = cellValues.row.Game_ID
                        window.location.href=`/endzone/data/delete?gameId=${gameId}`
                    }}>Delete</Button>
                )
            }},
          ];
            
    
    }

    componentDidMount = () => {
        this.getFormations()
    }

    handleError = (response) => {
        try {
            console.log(response)
            this.setState({alert: {
                style: {display: "block"},
                severity: "error",
                userMessage: response.response.data
            }})
        }
        catch {
            this.setState({alert: {
                style: {display: "block"},
                severity: "error",
                userMessage: "Unexpected Error - Please Contact: endzone.support@gmail.com"
            }})
        }
    }

    getFormations = () => {
        axios.get('/endzone/data/game/get').then((response) => {
            console.log(response.data)
            this.setState({rows:response.data})
        },(reject) => {
            this.handleError(reject)
        })
    }
    closeAlert = () => {
        let alert =  {
            style: {display: "none"},
            severity: "info",
            userMessage: ""
        }
        this.setState(
            {alert : alert}
        )
    }
    render() {
        
        return(
            <div className='manage-page'> 
                <div className = 'top-nav-container'>
                    <EndzoneNav links={[{title: "Return to the Data Hub", link: "/endzone/data/hub"},]}></EndzoneNav>
                </div>
                <div className = 'manage-data-divider'>
                    <div className='alert-div' style = {this.state.alert.style}>
                        <Alert variant="filled" severity={this.state.alert.severity}
                            action={
                                <IconButton
                                    aria-label="close"
                                    color="inherit"
                                    size="large"
                                    onClick={() => {
                                        this.closeAlert();
                                    }}
                                >
                                <CloseIcon fontSize="inherit" />
                                </IconButton>
                            }>
                            {this.state.alert.userMessage}
                        </Alert>
                    </div>
                    <div className = "top-buttons-bar">
                        <div className = "endzone-search-bar-div">
                            <EndzoneSearchBar className = "search-bar" style = {{"font-size":"2.5vh", "color":"#0984e3", "background-color":"#353b48"}}></EndzoneSearchBar>
                        </div>
                        <div className = "top-right-buttons">
                            
                            <EndzoneButton  className = "new-game-button" style={{"margin": "1vh"}} text = "New Game" icon = {<AddIcon sx = {{"font-size": '150%'}}></AddIcon>} onClick = {(e)=>{
                                window.location.href = `/endzone/data/game/create`
                            }}></EndzoneButton>
                            <EndzoneButton  className = "upload-game-button" style={{"margin": "1vh", }} text = "Upload Game" icon = {<AddIcon sx = {{"font-size": '150%'}} ></AddIcon>} onClick = {(e)=>{
                                window.location.href = `/endzone/data/game/upload`
                            }}></EndzoneButton>
                        </div>
                    </div>
                    <div className = "table-grid">
                        <DataGrid className = 'data-grid' style = {{"color" : "black","font-size":"1.85vh","backgroundColor" : 'white', "height":"100%", "width" : "100%", "border": "3px solid #0984e3", "fontFamily":"Audiowide"}} rows={this.state.rows} columns={this.columns}></DataGrid>
                    </div>
                    
            
                </div>
                <div className = "footer">
                    <Footer></Footer>
                </div>
                
                
                
            </div>
        );
    }
}



export default ManageData;